.default-layout {
    display: grid;
    grid-template-rows: 80px auto 3rem;
    grid-template-areas:
    "header"
    "main"
    "footer";
}

header {
    grid-area: header;
}

main {
    grid-area: main;
    min-height: 88vh;
    margin-top: 10px
}

footer {
    grid-area: footer;
    background: black;
    padding: 1rem;
    color: white;
}